.button-wrapper-conditions {

		.right-button {
			align-self: flex-end;

			@media only screen and (max-width: 526px) {
				align-self: center;
				width: 100%;
				margin-top: 20px; } } }

.resume-form-textarea {
	margin-bottom: 17px;
	margin-top: 7px;
	color: #333333;
	font-family: "BebasNeueRegular", Arial, sans-serif;
	font-size: 22px;
	line-height: 24px;

	@media only screen and (max-width: 460px) {
		display: none; } }






.row-form {
	display: flex;
	flex-direction: row;

	input[type="text"]:first-child {
		margin-right: 20px; }

	@media only screen and (max-width: 767px) {
		flex-direction: column; } }

.button-wrapper-conditions {
	display: flex;
	flex-direction: column;
	justify-content: flex-start;
	width: 100%;

	@media only screen and (max-width: 767px) {
		width: 100%;
		margin-right: 0; } }

.form-wrapper {
	display: flex;
	flex-direction: row;
	justify-content: space-between;
	align-items: flex-end;

	@media only screen and (max-width: 767px) {
		width: 100%;
		display: flex;
		flex-direction: column;
		justify-content: flex-start; } }


.btn-close {
	position: absolute;
	content: '';
	top: -36px;
	right: 0;
	width: 16px;
	height: 16px;
	cursor: pointer;
	transition: .5s ease-out;

	@media only screen and (max-width: 767px) {
		right: 15px;
		top: 15px; }


	&::after {
		position: absolute;
		content: '';
		width: 16px;
		height: 2px;
		background-color: #c4c4c4;
		left: 0;
		top: 8px;
		transform: rotate(-45deg); }

	&::before {
		position: absolute;
		content: '';
		width: 16px;
		height: 2px;
		background-color: #c4c4c4;
		left: 0;
		top: 8px;
		transform: rotate(45deg); }

	&:hover {

		opacity: 0.5;
		transition: .5s ease-out; } }





.overlay-popup {
	position: fixed;
	left: 0;
	top: 0;
	width: 100%;
	height: 100%;
	background-color: #000000;
	opacity: .8;
	z-index: 900;
	display: none; }

.resume-form-show {
	display: block;
	transition: .8s ease-out; }



.red {
	width: 100%;
	height: 600px;
	background-color: red; }

.top-section {
	display: flex;
	min-height: 640px;
	padding-bottom: 130px;
	width: 100%;
	justify-content: space-between;
	flex-direction: column;
	background-color: $light-gray;
	background: url(../img/Main_screen.png) no-repeat center;
	background-position: 0 top;
	background-size: cover;


