		height: 720px;
		padding-bottom: 0px; }

	@media only screen and (max-width: 360px) {
		padding-bottom: 0px;
		margin-bottom: 60px; } }

.button-wrapper {
	margin-right: 20px;
	flex-grow: 2;

	@media only screen and (max-width: 767px) {
		margin-right: 0; } }

