.index-sticky {
	position: fixed;
	font-family: "BebasNeueBold", Arial, sans-serif;
	color: $white;
	font-size: 26px;
	line-height: 36px;
	width: 100%;
	background: linear-gradient(to right, $dark-blue, $dark-orange);
	height: 68px;
	color: $white;
	text-align: center;
	left: 0;
	bottom: 0;
	z-index: 700;
	visibility: hidden;
	opacity: 0;
	display: none; }

.show-sticky {
	@media only screen and (max-width: 767px) {
		display: block;
		visibility: visible;
		opacity: 1;
		transition: .2s ease-out; } }

