.form-main {
	position: relative;
	padding-top: 110px;
	width: 100%;
	display: flex;
	flex-direction: row;
	justify-content: center;
	background: linear-gradient(to right, $dark-blue, $dark-orange);



	@media only screen and (max-width: 1200px) {
		flex-direction: column;
		padding-top: 70px; }

	@media only screen and (max-width: 767px) {
		padding-top: 50px; }


	&.footer-higher {
		@media only screen and (max-width: 1200px) {
			padding-top: 137px;

			&::before {
				position: absolute;
				content: '';
				width: 100%;
				height: 1px;
				background-color: rgba(255,255,255,.2);
				top: 70px;
				left: 0;
				visibility: visibility;
				opacity: 1; } } } }


.form-address {
	width: 60%;
	display: flex;
	flex-direction: row;
	justify-content: flex-end;
	margin-right: 115px;
	position: relative;

	@media only screen and (max-width: 1200px) {
		width: 100%;
		order: 1;
		justify-content: center;
		margin-right: 0;
		margin-top: 58px; }

	@media only screen and (max-width: 767px) {
		flex-direction: column;
		align-items: stretch;
		padding-left: 30px;
		padding-right: 30px; }

	@media only screen and (max-width: 360px) {
		margin-top: 50px; }

	&::before {

		@media only screen and (max-width: 1200px) {
			position: absolute;
			content: "";
			width: 100%;
			max-width: 618px;
			transform: translateX(-50%);
			height: 2px;
			background-color: #c4c4c4;
			left: 50%;
			top: -58px; }

		@media only screen and (max-width: 767px) {
			width: 90%;
			max-width: 90%; } }


	&-inner {
		display: flex;
		flex-direction: column;
		max-width: 242px;
		padding-top: 7px;

		@media only screen and (max-width: 767px) {
			margin-bottom: 30px;
			text-align: center;
			max-width: 100%; } }



	&-img {
		margin-right: 60px;
		width: 270px;
		height: 410px;
		background: url('../img/sprite.png') no-repeat;
		background-position: 0px -1026px;

		@media only screen and (max-width: 767px) {
			margin-right: 0;
			order: 1;
			align-self: center;
			width: 270px;
			height: 292px; } } }



.form-question {
	width: 40%;
	margin-left: 100px;
	margin-bottom: 80px;
	position: relative;

	@media only screen and (max-width: 1200px) {
		width: 100%;
		justify-content: center;
		margin-left: 0;
		margin-bottom: 58px; }

	@media only screen and (max-width: 767px) {
		align-items: stretch;
		padding-left: 30px;
		padding-right: 30px; }

	@media only screen and (max-width: 360px) {
		margin-bottom: 50px; }

	&::before {
		position: absolute;
		content: "";
		width: 2px;
		height: 350px;
		background-color: #c4c4c4;
		left: -101px;
		top: -11px;

		@media only screen and (max-width: 1200px) {
			display: none; } }

	&-inner {
		display: flex;
		flex-direction: column;
		padding-top: 7px;

		@media only screen and (max-width: 1200px) {
			flex-direction: row;
			justify-content: center; }

		@media only screen and (max-width: 767px) {
			flex-direction: column;
			align-items: center;
			align-items: stretch; } } }

.agree-label {
	position: relative;
	padding-left: 25px;
	cursor: pointer; }

.agree-label input[type=checkbox] + .checkbox-indicator {
	width: 13px;
	height: 13px;
	border: 1px solid #BDBDBD;
	border-radius: 50%;
	position: absolute;
	left: 2px;
	top: 2px; }


.agree-label input[type=checkbox]:checked + .checkbox-indicator::before {
	content: '';
	width: 5px;
	height: 5px;
	position: absolute;
	background-color: #820333;
	border-radius: 50%;
	top: 50%;
	left: 50%;
	transform: translate(-50%, -50%); }

input[type=checkbox] {

	&.visually-hidden {
		position: absolute;
		width: 1px;
		height: 1px;
		margin: -1px;
		border: 0;
		padding: 0;
		white-space: nowrap;
		clip-path: inset(100%);
		clip: rect(0 0 0 0);
		overflow: hidden; } }

