@charset "UTF-8";

@import url(normalize.css);

@import url(fonts.css);

html,
body {
  overflow-x: hidden;
  height: auto;
}

* {
  box-sizing: border-box;
}

p {
  font-family: "FiraSansRegular", Arial, sans-serif;
  font-size: 16px;
  line-height: 20px;
  color: #ffffff;
}

ul,
li {
  margin: 0;
  padding: 0;
  list-style: none;
}

.resume-form {
  display: none;
  position: fixed;
  color: #333333;
  background-color: #ffffff;
  z-index: 1000;
  width: 620px;
  padding: 50px 40px 30px 40px;
  left: 50%;
  top: 50%;
  transform: translate(-50%, -50%);
  border-radius: 4px;
}

.resume-form h2 {
  font-family: "BebasNeueBold", Arial, sans-serif;
  font-size: 26px;
  color: #333333;
  line-height: 34px;
  margin-top: 0;
  margin-bottom: 12px;
}

.resume-form-text {
  color: #333333;
  font-family: "FiraSansBold", Arial, sans-serif;
  font-size: 16px;
  line-height: 24px;
  margin-top: 0;
  margin-bottom: 20px;
}

.resume-form input[type="text"] {
  width: 100%;
  padding: 15px 20px 15px 23px;
  color: #333333;
  font-family: "BebasNeueRegular", Arial, sans-serif;
  font-size: 22px;
  line-height: 26px;
  border: 2px solid #E5E5E5;
  border-radius: 0px;
  margin-bottom: 10px;
  box-shadow: none;
}

.resume-form input[type="text"]::placeholder {
  color: rgba(51, 51, 51, 0.3);
}

.resume-form input[type="tel"] {
  width: 100%;
  padding: 15px 20px 15px 23px;
  color: #333333;
  font-family: "BebasNeueRegular", Arial, sans-serif;
  font-size: 22px;
  line-height: 26px;
  border: 2px solid #E5E5E5;
  border-radius: 0px;
  margin-bottom: 10px;
  box-shadow: none;
}

.resume-form input[type="tel"]::placeholder {
  color: rgba(51, 51, 51, 0.3);
}

.resume-form .offer-button {
  margin-top: 20px;
  outline: none;
}

.resume-form .offer-button:focus {
  outline: none;
}

.resume-form .button-gray {
  margin-top: 0;
  outline: none;
}

.hide {
  width: 0.1px;
  height: 0.1px;
  opacity: 0;
  overflow: hidden;
  position: absolute;
  z-index: -1;
}

