  background-color: transparent;
  border: 2px solid #f43737;
  transition: .2s ease-out;
}

.button-bottom {
  margin-top: auto;
}

.resume-form-conditions {
  display: none;
  position: fixed;
  color: #000000;
  background-color: #ffffff;
  z-index: 1000;
  width: 620px;
  padding: 50px 40px 30px 40px;
  left: 50%;
  top: 50%;
  border-radius: 4px;
  transform: translate(-50%, -50%);
}

.resume-form-conditions h2 {
  font-family: "BebasNeueBold", Arial, sans-serif;
  font-size: 26px;
  line-height: 26px;
  color: #333333;
  margin-top: 0;
  margin-bottom: 8px;
}

.resume-form-conditions-text {
  color: #333333;
  font-family: "FiraSansBold", Arial, sans-serif;
  font-size: 16px;
  line-height: 18px;
  margin-top: 0;
  margin-bottom: 20px;
}

.resume-form-conditions input[type="text"] {
  width: 100%;
  padding: 15px 20px 15px 23px;
  color: #333333;
  font-family: "BebasNeueRegular", Arial, sans-serif;
  font-size: 22px;
  line-height: 26px;
  border: 2px solid #E5E5E5;
  margin-bottom: 10px;
  border-radius: 0px;
  box-shadow: none;
}

.resume-form-conditions input[type="text"]::placeholder {
  color: rgba(51, 51, 51, 0.3);
}

.resume-form-conditions input[type="tel"] {
  width: 100%;
  padding: 15px 20px 15px 23px;
  color: #333333;
  font-family: "BebasNeueRegular", Arial, sans-serif;
  font-size: 22px;
  line-height: 26px;
  border: 2px solid #E5E5E5;
  margin-bottom: 10px;
  border-radius: 0px;
  box-shadow: none;
}

.resume-form-conditions input[type="tel"]::placeholder {
  color: rgba(51, 51, 51, 0.3);
}

.resume-form-conditions .offer-button {
  margin-top: 20px;
}

.resume-form-conditions .button-gray {
  margin-top: 0;
}

.resume-form-conditions textarea {
  width: 100%;
  padding: 18px 20px 18px 23px;
  color: #333333;
  font-family: "FiraSansRegular", Arial, sans-serif;
  font-size: 14px;
  line-height: 18px;
  border: 2px solid #E5E5E5;
  margin-bottom: 0;
  resize: none;
}

.resume-form-conditions textarea::placeholder {
  color: rgba(51, 51, 51, 0.3);
  font-family: "BebasNeueRegular", Arial, sans-serif;
  font-size: 22px;
}

.button-wrapper-conditions .right-button {
  align-self: flex-end;
}

.resume-form-textarea {
  margin-bottom: 17px;
  margin-top: 7px;
  color: #333333;
  font-family: "BebasNeueRegular", Arial, sans-serif;
  font-size: 22px;
  line-height: 24px;
}

.row-form {
  display: flex;
  flex-direction: row;
}

.row-form input[type="text"]:first-child {
  margin-right: 20px;
}

.button-wrapper-conditions {
  display: flex;
  flex-direction: column;
  justify-content: flex-start;
  width: 100%;
}

.form-wrapper {
  display: flex;
  flex-direction: row;
  justify-content: space-between;
  align-items: flex-end;
}

.btn-close {
  position: absolute;
  content: '';
  top: -36px;
  right: 0;
  width: 16px;
  height: 16px;
  cursor: pointer;
  transition: .5s ease-out;
}

.btn-close::after {
  position: absolute;
  content: '';
  width: 16px;
  height: 2px;
  background-color: #c4c4c4;
  left: 0;
  top: 8px;
  transform: rotate(-45deg);
}

.btn-close::before {
  position: absolute;
  content: '';
  width: 16px;
  height: 2px;
  background-color: #c4c4c4;
  left: 0;
  top: 8px;
  transform: rotate(45deg);
}

.btn-close:hover {
  opacity: 0.5;
  transition: .5s ease-out;
}

.overlay-popup {
  position: fixed;
  left: 0;
  top: 0;
  width: 100%;
  height: 100%;
  background-color: #000000;
  opacity: .8;
  z-index: 900;
  display: none;
}

.resume-form-show {
  display: block;
  transition: .8s ease-out;
}

.red {
  width: 100%;
  height: 600px;
  background-color: red;
}

.top-section {
  display: flex;
  min-height: 640px;
  padding-bottom: 130px;
  width: 100%;
  justify-content: space-between;
  flex-direction: column;
  background-color: #e4e4e4;
  background: url(../img/Main_screen.png) no-repeat center;
  background-position: 0 top;
  background-size: cover;
}

.button-wrapper {
  margin-right: 20px;
  flex-grow: 2;
}

.top-header {
  width: 100%;
  min-height: 60px;
  display: flex;
  justify-content: space-between;
  padding: 12px 20px;
}

.top-header-logo {
  display: flex;
  flex-direction: column;
  flex-wrap: nowrap;
  width: 30%;
  min-height: 45px;
}

.top-header-rewards {
