  font-family: "BebasNeueBold", Arial, sans-serif;
  color: #ffffff;
  font-size: 26px;
  line-height: 36px;
  width: 100%;
  background: linear-gradient(to right, #060333, #492424);
  height: 68px;
  color: #ffffff;
  text-align: center;
  left: 0;
  bottom: 0;
  z-index: 700;
  visibility: hidden;
  opacity: 0;
  display: none;
}

.first-video {
  background: url("../img/poster.jpg") no-repeat center;
  background-size: cover;
}

.second-video {
  background: url("../img/poster.jpg") no-repeat center;
  background-size: cover;
}

.third-video {
  background: url("../img/poster.jpg") no-repeat center;
  background-size: cover;
}

.orange {
  color: #FFE34E !important;
}

.form-main {
  position: relative;
  padding-top: 110px;
  width: 100%;
  display: flex;
  flex-direction: row;
  justify-content: center;
  background: linear-gradient(to right, #060333, #492424);
}

.form-address {
  width: 60%;
  display: flex;
  flex-direction: row;
  justify-content: flex-end;
  margin-right: 115px;
  position: relative;
}

.form-address-inner {
  display: flex;
  flex-direction: column;
  max-width: 242px;
  padding-top: 7px;
}

.form-address-img {
  margin-right: 60px;
  width: 270px;
  height: 410px;
  background: url("../img/sprite.png") no-repeat;
  background-position: 0px -1026px;
}

.form-question {
  width: 40%;
  margin-left: 100px;
  margin-bottom: 80px;
  position: relative;
}

.form-question::before {
  position: absolute;
  content: "";
  width: 2px;
  height: 350px;
  background-color: #c4c4c4;
  left: -101px;
  top: -11px;
}

.form-question-inner {
  display: flex;
  flex-direction: column;
  padding-top: 7px;
}

.agree-label {
  position: relative;
  padding-left: 25px;
  cursor: pointer;
}

.agree-label input[type=checkbox] + .checkbox-indicator {
  width: 13px;
  height: 13px;
  border: 1px solid #BDBDBD;
  border-radius: 50%;
  position: absolute;
  left: 2px;
  top: 2px;
}

.agree-label input[type=checkbox]:checked + .checkbox-indicator::before {
  content: '';
  width: 5px;
  height: 5px;
  position: absolute;
  background-color: #820333;
  border-radius: 50%;
  top: 50%;
  left: 50%;
  transform: translate(-50%, -50%);
}

input[type=checkbox].visually-hidden {
  position: absolute;
  width: 1px;
  height: 1px;
  margin: -1px;
  border: 0;
  padding: 0;
  white-space: nowrap;
  clip-path: inset(100%);
  clip: rect(0 0 0 0);
  overflow: hidden;
}

.form-header {
  font-family: "BebasNeueBold", Arial, sans-serif;
  font-size: 28px;
  color: #ffffff;
  line-height: 28px;
  margin: 0 0 22px;
}

.form-header-wrapper {
  max-width: 242px;
}

.input-wrapper {
  max-width: 242px;
  display: flex;
  flex-direction: column;
}

.red-text {
  color: #e13434;
}

.main-form-field {
  width: 100%;
  padding: 15px 20px 15px 23px;
  color: #333333;
  font-family: "BebasNeueRegular", Arial, sans-serif;
  font-size: 22px;
  line-height: 26px;
  border: 2px solid rgba(0, 0, 0, 0.3);
  border-radius: 2px;
  margin-bottom: 10px;
  box-shadow: none;
}

.main-form-field::placeholder {
  color: rgba(0, 0, 0, 0.5);
}

.main-form-submit {
  width: 100%;
  text-align: center;
  padding: 0;
  color: #ffffff;
  font-family: "BebasNeueRegular", Arial, sans-serif;
  font-size: 26px;
  line-height: 60px;
  border: none;
  border-radius: 4px;
  margin-bottom: 16px;
  box-shadow: none;
  background-color: #e13434;
  cursor: pointer;
  transition: .2s ease-out;
}

.main-form-submit:disabled {
  cursor: not-allowed;
  background-color: #c59e9e;
}

.main-form-submit:disabled:hover {
  cursor: not-allowed;
  background-color: #c59e9e;
}

.main-form-submit:hover {
  background-color: #f43737;
  transition: .2s ease-out;
}

.main-form-submit:focus {
  outline: none;
}

.main-form-description {
  font-family: "FiraSansRegular",Arial, sans-serif;
  font-size: 14px;
  color: #c4c4c4;
  line-height: 16px;
  margin-top: 0;
  margin-bottom: 24px;
}

.agree-label {
  font-family: "FiraSansBold", Arial, sans-serif;
  font-size: 12px;
  line-height: 14px;
  color: #838383;
}

